import React, { useEffect, useState } from 'react';
import { Link, useNavigate } from 'react-router-dom';
import { useAuth } from '../contexts/AuthContext';

import './Header.css';
import {
    Container,
    Nav,
    Navbar,
    NavDropdown,
    Offcanvas,
} from 'react-bootstrap';

const screenwidth = window.innerWidth;

const Header = () => {
    const { currentUser, logout, clearError } = useAuth();
    const [error, setError] = useState();

    const navigate = useNavigate();

    const handleLogout = async () => {
        setError('');
        try {
            await logout();
            navigate('/');
        } catch (err) {
            setError(
                err.message
                    .replace(/^Firebase:\s*/, '')
                    .replace(/\s*\(.*?\)\s*./g, '')
            );
        }
    };

    useEffect(() => {
        if (error) {
            alert(error);
        }
    }, [error]);

    return (
        <Navbar as='header' expand='sm' collapseOnSelect>
            <Container>
                <Navbar.Brand as={Link} to='/' onClick={clearError}>
                    ToDo List
                </Navbar.Brand>
                <Navbar.Toggle aria-controls='offcanvasNavbar-expand-sm' />
                <Navbar.Offcanvas
                    aria-labelledby='offcanvasNavbarLabel-expand-sm'
                    placement='end'
                >
                    <Offcanvas.Header closeButton />
                    <Offcanvas.Body>
                        <Nav className='justify-content-end flex-grow-1 pe-3'>
                            {currentUser ? (
                                <>
                                    {screenwidth < 576 ? (
                                        <>
                                            <Nav.Link
                                                as={Link}
                                                to='/profile'
                                                href
                                                onClick={clearError}
                                            >
                                                Profile
                                            </Nav.Link>
                                            <Nav.Link as={Link} to='' href>
                                                Link
                                            </Nav.Link>
                                            <Nav.Link as={Link} to='' href>
                                                Link
                                            </Nav.Link>
                                            <Nav.Link
                                                as={Link}
                                                href
                                                onClick={handleLogout}
                                            >
                                                Logout
                                            </Nav.Link>
                                        </>
                                    ) : (
                                        <NavDropdown
                                            title={currentUser.displayName}
                                        >
                                            <NavDropdown.Item
                                                as={Link}
                                                to='/profile'
                                                onClick={clearError}
                                            >
                                                Profile
                                            </NavDropdown.Item>
                                            <NavDropdown.Item as={Link} to=''>
                                                Link
                                            </NavDropdown.Item>
                                            <NavDropdown.Item as={Link} to=''>
                                                Link
                                            </NavDropdown.Item>
                                            <NavDropdown.Divider />
                                            <NavDropdown.Item
                                                onClick={handleLogout}
                                            >
                                                Logout
                                            </NavDropdown.Item>
                                        </NavDropdown>
                                    )}
                                </>
                            ) : (
                                <>
                                    <Nav.Link
                                        as={Link}
                                        to='/login'
                                        href
                                        onClick={clearError}
                                    >
                                        Log In
                                    </Nav.Link>
                                    <Nav.Link
                                        as={Link}
                                        to='/signup'
                                        href
                                        onClick={clearError}
                                    >
                                        Sign Up
                                    </Nav.Link>
                                </>
                            )}
                        </Nav>
                    </Offcanvas.Body>
                </Navbar.Offcanvas>
            </Container>
        </Navbar>
    );
};

export default Header;
