import { BrowserRouter as Router, Routes, Route } from 'react-router-dom';
import { Suspense, lazy } from 'react';

import { AuthProvider } from './contexts/AuthContext';
import './index.css';
import './App.css';

import { Header } from './Sections/index';
import Spinner from 'react-bootstrap/Spinner';

// Lazy load pages
const TasksPage = lazy(() => import('./Pages/TasksPage/TasksPage'));
const PrivateTasksPage = lazy(() =>
    import('./Pages/TasksPage/PrivateTasksPage')
);
const Signup = lazy(() => import('./Pages/Account/Signup'));
const PrivateSignin = lazy(() => import('./Pages/Account/PrivateSignin'));
const Login = lazy(() => import('./Pages/Account/Login'));
const Profile = lazy(() => import('./Pages/Account/Profile'));
const PrivateRoute = lazy(() => import('./Pages/Account/PrivateRoute'));

const ResetPassword = lazy(() =>
    import('./Pages/Account/components/ResetPassword')
);
const ChangePassword = lazy(() =>
    import('./Pages/Account/components/ChangePassword')
);

const ForgotPassword = lazy(() =>
    import('./Pages/Account/components/ForgotPassword')
);
const NotFound = lazy(() => import('./Pages/NotFound/NotFound'));

function App() {
    return (
        <Router basename={process.env.PUBLIC_URL}>
            <AuthProvider>
                <Suspense
                    fallback={
                        <div className='spinner-container'>
                            <Spinner animation='border' variant='primary' />
                        </div>
                    }
                >
                    <Header />
                    <Routes>
                        <Route element={<PrivateTasksPage />}>
                            <Route path='/*' element={<TasksPage />} />
                        </Route>
                        <Route element={<PrivateRoute />}>
                            <Route path='/profile' element={<Profile />} />
                            <Route
                                path='/profile/reset-password'
                                element={<ResetPassword />}
                            />
                            <Route
                                path='/profile/change-password'
                                element={<ChangePassword />}
                            />
                        </Route>
                        <Route element={<PrivateSignin />}>
                            <Route
                                path='to-do-list/signup'
                                element={<Signup />}
                            />
                            <Route
                                path='to-do-list/login'
                                element={<Login />}
                            />
                            <Route
                                path='/forgot-password'
                                element={<ForgotPassword />}
                            />
                        </Route>

                        <Route path='*' element={<NotFound />} />
                    </Routes>
                    {/* <Footer /> */}
                </Suspense>
            </AuthProvider>
        </Router>
    );
}

export default App;
